import RegisterForm from '@/app/ui/register-form';
import { Package, TrendingUp, ShieldCheck } from 'lucide-react';

export default function RegisterPage() {
    return (
        <div className="min-h-screen w-full flex">
            {/* Left Side - Product Showcase */}
            <div className="hidden lg:flex lg:w-1/2 bg-slate-900 relative overflow-hidden flex-col justify-between p-12 text-white">
                {/* Background Effects */}
                <div className="absolute top-0 right-0 w-96 h-96 bg-blue-600/20 rounded-full blur-3xl -mr-20 -mt-20" />
                <div className="absolute bottom-0 left-0 w-96 h-96 bg-indigo-600/20 rounded-full blur-3xl -ml-20 -mb-20" />

                {/* Branding */}
                <div className="relative z-10 flex items-center gap-3">
                    <div className="bg-blue-600 p-2 rounded-lg">
                        <Package className="w-6 h-6 text-white" />
                    </div>
                    <span className="text-2xl font-bold tracking-tight">Kargo</span>
                </div>

                {/* Main Content */}
                <div className="relative z-10 max-w-lg">
                    <h1 className="text-5xl font-bold mb-6 leading-tight">
                        Join the Future of Inventory Management
                    </h1>
                    <p className="text-slate-400 text-lg mb-8 leading-relaxed">
                        Create an account to access powerful tools for tracking, forecasting, and optimizing your supply chain.
                    </p>

                    <div className="space-y-4">
                        <div className="flex items-center gap-4 p-4 rounded-xl bg-white/5 border border-white/10 backdrop-blur-sm">
                            <div className="bg-emerald-500/20 p-2 rounded-lg">
                                <TrendingUp className="w-5 h-5 text-emerald-400" />
                            </div>
                            <div>
                                <h3 className="font-semibold">Start Tracking Today</h3>
                                <p className="text-sm text-slate-400">Instant access to analytics</p>
                            </div>
                        </div>

                        <div className="flex items-center gap-4 p-4 rounded-xl bg-white/5 border border-white/10 backdrop-blur-sm">
                            <div className="bg-blue-500/20 p-2 rounded-lg">
                                <ShieldCheck className="w-5 h-5 text-blue-400" />
                            </div>
                            <div>
                                <h3 className="font-semibold">Secure Platform</h3>
                                <p className="text-sm text-slate-400">Your data is safe with us</p>
                            </div>
                        </div>
                    </div>
                </div>

                {/* Footer */}
                <div className="relative z-10 flex items-center gap-2 text-sm text-slate-500">
                    <span>© 2024 Kargo Inc.</span>
                    <span>•</span>
                    <a href="#" className="hover:text-white transition-colors">Privacy Policy</a>
                    <span>•</span>
                    <a href="#" className="hover:text-white transition-colors">Terms of Service</a>
                </div>
            </div>

            {/* Right Side - Registration Form */}
            <div className="w-full lg:w-1/2 bg-white flex items-center justify-center p-8">
                <div className="w-full max-w-md">
                    <RegisterForm />
                </div>
            </div>
        </div>
    );
}
